import React, { useState, useEffect } from 'react'
import goup from '../images/up-white.png'

export default function GoUp() {
	const [showGoUpButton, setShowGoUpButton] = useState(false);

	useEffect(() => {
		window.addEventListener('scroll', () => {
			if (window.scrollY > 300) {
				setShowGoUpButton(true);
			} else {
				setShowGoUpButton(false);
			}
		});
	}, []);

	const goUp = () => {
		window.scrollTo({
			top: 0,
			behavior: 'smooth',
		});
	};

	return (
		<div id="go-up-container">
			{showGoUpButton && (<button id="button--go-up" onClick={goUp}>
				<img src={goup} className="nav-slim--button-icon" alt="GoUp" />
			</button>)}
		</div>
	);
}
