import React, { useState, useEffect } from 'react'
import Content from './SettingsContent'
import gear from '../images/gear-black.png'
import exit from '../images/x-white.png'

export default function Settings() {
	const [showSettings, setShowSettings] = useState(false);

	const toggleSettings = () => {
		setShowSettings(showSettings? false : true);
	};

	return (
		<div id="settings-container">
			<button id="button--settings" onClick={toggleSettings}>
				<img src={gear} className="settings--button-icon" alt="Settings" />
			</button>
			{showSettings && (<div id="settings-rim" onClick={toggleSettings}></div>)}
			{showSettings && (<div id="settings">
				<Content />
				<button id="settings-content-button-exit" onClick={toggleSettings}>
					<img src={exit} className="nav-slim--button-icon" alt="Exit" />
				</button></div>)}
		</div>
	);
}
