import { Outlet, Link } from "react-router-dom"
import { useState, useEffect } from "react"
import email from '../images/envelope-black.png'
import github from '../images/github-black.png'
import linkedin from '../images/linkedin-black.png'
import more from '../images/more-black.png'
import exit from '../images/x-black.png'

function Navbar() {
	const [showMore, setMore] = useState(false);

	const [isMobile, setMobile] = useState(window.innerWidth <= 720? true : false);

	
	useEffect(() => {
		setMobile(window.innerWidth <= 720? true : false)
	}, [])

	useEffect(() => {
		function handleResize() {
			setMobile(window.innerWidth <= 720? true : false);
			setMore(false);
		}

		window.addEventListener("resize", handleResize);
		return () => window.removeEventListener("resize", handleResize);
	}, []);

	const toggleMore = () => {
		setMore(showMore? false : true);
		console.log(showMore);
	};
	return (
		<>
		<div className="NavbarSlim">
			<div className="nav-slim-container" id="nav-slim-container">
				<div className="nav-slim--top-section">
					<Link className="webname-slim" to="/">Rafael Mikayelyan</Link>
				</div>
				{isMobile && (<button className="nav-slim--button-more" onClick={toggleMore}>
						<img src={more} className="nav-slim--button-icon" alt="More" />
					</button>)}
				{!isMobile && (
				<div className="nav-slim--bottom-section">
					<Link to="/Projects" className="nav-slim--button">Projects</Link>
					<Link to="/Archive" className="nav-slim--button">Archive</Link>
					<div className="nav-slim--icons">
						<a href="mailto:rafaelmikayelyan.web@gmail.com"  target="_blank" rel="noopener noreferrer" className="nav-slim--button">
							<img src={email} className="nav-slim--button-icon" alt="Email" />
						</a>
						<a href="https://github.com/rafaelmikayelyan" target="_blank" rel="noopener noreferrer">
							<img src={github} className="nav-slim--button-icon" alt="GitHub" />
						</a>
						<a href="https://www.linkedin.com/in/rafael-mikayelyan/" target="_blank" rel="noopener noreferrer">
							<img src={linkedin} alt="LinkedIn" className="nav-slim--button-icon" />
						</a>
					</div>
				</div>
				)}
			</div>
			{isMobile && showMore && (
				<div className="nav-slim--more-menu">
					<div className="nav-slim--more-top">
						<Link className="webname-slim" to="/" onClick={toggleMore}>Rafael Mikayelyan</Link>
						<button id="nav-slim--more-menu-button-exit" onClick={toggleMore}>
							<img src={exit} className="nav-slim--button-icon" alt="Exit" />
						</button>
					</div>
					<div className="nav-slim--more-center">
						<Link to="/Projects" className="nav-slim--button" onClick={toggleMore}>Projects</Link>
						<Link to="/Archive" className="nav-slim--button" onClick={toggleMore}>Archive</Link>
					</div>
					<div className="nav-slim--more-icons">
						<a href="mailto:rafaelmikayelyan.web@gmail.com"  target="_blank" rel="noopener noreferrer" className="nav-slim--button">
							<img src={email} className="nav-slim--button-icon" alt="Email" />
						</a>
						<a href="https://github.com/rafaelmikayelyan" target="_blank" rel="noopener noreferrer">
							<img src={github} className="nav-slim--button-icon" alt="GitHub" />
						</a>
						<a href="https://www.linkedin.com/in/rafael-mikayelyan/" target="_blank" rel="noopener noreferrer">
							<img src={linkedin} alt="LinkedIn" className="nav-slim--button-icon" />
						</a>
					</div>
				</div>
			)}
		</div>
		<Outlet />
		</>
	);
}

export default Navbar;
