import { useState, useRef, useEffect } from 'react'
import React from 'react'
import * as d3 from 'd3'
import minImg from '../images/min.png'
import maxImg from '../images/max.png'

function SortingAlgo() {
	const canvasSortRef = useRef(null);
	const ctxRef = useRef(null);
	const canvasH = 400;
	const canvasW = window.innerWidth;
	const rangeMin = 20, rangeMax = 100;
	const [rangeVal, setRangeVal] = useState((rangeMax - rangeMin) / 2 + rangeMin);
	const [data, setData] = useState(null);
	const [barW, setBarW] = useState(null);

	const renderLog = [];


	function createRandomArray(value) {
		if (!value) {
			value = rangeVal
		}
		let randomArray = new Array();
		for (let i = 0; i < value; i++) {
			randomArray[i] = Math.floor(Math.random() * 400);
		}
		setData(randomArray)
		return randomArray;
	}

	useEffect(() => {
		const canvas = canvasSortRef.current;
		canvas.width = canvasW;
		canvas.height = canvasH;
		canvas.style.width = '${canvasW}px';
		canvas.style.height = '${canvasH}px';

		const ctx = canvas.getContext("2d")
		ctx.scale(1,1);

		ctxRef.current = ctx;
		drawGraph(ctx, createRandomArray());
	}, [])

	const refreshGraph = () => {
		ctxRef.current.clearRect(0, 0, canvasW, canvasH);
		drawGraph(ctxRef.current, createRandomArray(rangeVal));
	}

	function newGraph(value) {
		ctxRef.current.clearRect(0, 0, canvasW, canvasH);
		drawGraph(ctxRef.current, createRandomArray(value));
	}

	const newRangeVal = (event) => {
		let value = event.target.value;
		setRangeVal(value);
		newGraph(value);
	}

	const drawGraph = (ctx, bars) => {
		let padding = 100 / bars.length;
		let barW = canvasW / bars.length - padding;
		let barX = barW + padding;
		setBarW([barX, barW]);
		for (let i = 0; i < bars.length; i++) {
			ctx.fillStyle = "lightgray";
			ctx.fillRect(i * barX, canvasH - bars[i], barW, bars[i]);
		}
	}

	const selection = () => {
		// console.log(data)
		// console.log(data[0])
		// console.log(barW)

		// drawBarTest(1);

	}
	
	const drawBar = (bar) => {
		let padding = 100 / data.length;
		let barW = canvasW / data.length - padding;
		let barX = barW + padding;
		for (let i = 0; i < data.length; i++) {
			if (bar == i) {
				ctxRef.current.fillStyle = "red";
			} else {
				ctxRef.current.fillStyle = "lightgray";
			}
			ctxRef.current.fillRect(i * barX, canvasH - data[i], barW, data[i]);
		}
	}

	function drawBarTest(bar) {
		console.log(barW[0])
		console.log(barW[1])
		ctxRef.current.fillStyle = "red";
		ctxRef.current.fillRect(bar * barW[0], 300, barW[1], 30);
	}

	const bubble = () => {
		for (let i = 0; i < data.length - 1; i++) {
			for (let j = 0; j < data.length - i - 1; j++) {
				drawBar(j)
				// renderBarChange(j, 1);

				if (data[j] > data[j + 1]) {

					let temp = data[j];
					data[j] = data[j + 1];

					// renderBarChange(j, 1, array[j + 1])

					data[j + 1] = temp;

					// renderBarChange(j + 1, 2, temp)
					// await sleep();
				}

				// if (!isSorted) {
					// return;
				// }
				// renderBarChange(j, 3);
			}
		}
		// renderGraphAsSorted();
	}

	return (
		<div className="Algorithms">
			<canvas id="sortingCanvas" 
				ref={canvasSortRef}
			/>

			<div className='controls'>
				<div className='section--sort'>
					<button id='button--bubble' className='button--algo button--sort' onClick={bubble}>bubble sort</button>
					<button id='button--selection' className='button--algo button--sort' onClick={selection}>selection sort</button>
					<button id='button--merge' className='button--algo button--sort'>merge sort</button>
					<button id='button--insertion' className='button--algo button--sort'>insertion sort</button>
					<button id='button--quicksort' className='button--algo button--sort'>quicksort</button>
				</div>
				<div className='section--generate'>
					<button id="button--refresh" className='button--algo button--refresh' onClick={refreshGraph}></button>
					<div className="slider-container">
						<img src={minImg} id='slider-min' className='slider-icons'></img>
						<input type="range" min={rangeMin} max={rangeMax} defaultValue={rangeVal} className="slider" id="range" onChange={newRangeVal} />
						<img src={maxImg} id='slider-max' className='slider-icons'></img>
					</div>
				</div>
				<div className='section--discription'>
				<br />
					<p id='discription'>Hover over a sorting algorithm to know more!</p>
				</div>
			</div>
			
			<hr />
		<svg>
		</svg>

		</div>
	);
}

export default SortingAlgo;
