import Card from "./Card.js"
import data from "./projects-data.js"

export default function Projects() {
	const cards = data.map(item => {
		return (
			<Card
				key={item.id}
				{...item}
			/>
		)
	})
	return (
		<div className="Projects">
			<section className='cards-list'>
				{cards}
			</section>
		</div>

	);
}
